.testimonilas {
  text-align: center;
  max-width: 786px;

  background-image: url(../images/svg/Vector.png);
  background-repeat: no-repeat;
  background-position: top;

  &__text--d {
    font-family: Inter, sans-serif;
    font-size: 24px;
    font-weight: 700;
    line-height: 36px;
    text-align: center;

    @include on-mob-tabl {
      display: none;
    }
  }

  &__text--m {
    font-family: Inter, sans-serif;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    text-align: center;

    @include on-tablet {
      display: none;
    }
  }

  &__name {
    margin-top: 20px;
    margin-bottom: 5px;

    &--text {
      font-family: Inter, sans-serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
      text-align: center;
      color: #7c7c7c;
    }
  }
}
