.top-bar__aside {
  max-width: 620px;
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
}

.menu {
  @include on-tabl-desk {
    padding: 42px 34px;
  }

  @include on-mob-tabl {
    padding: 24px 30px;
  }

  padding: 30px 123px;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: $c-gray;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  transform: translateX(100%);

  &:target {
    display: block;
    opacity: 1;
    transform: translateX(0);
    pointer-events: all;
    z-index: 1;
  }
}

.page:has(.menu:target) {
  overflow: hidden;
}

.top-bar__menu--close {
  @include on-desktop {
    display: none;
  }

  background-image: url(../images/svg/Icon-Close.png);
  width: 24px;
  height: 24px;
  display: block;
  z-index: 1;
}

.nav__list--tab {
  width: 620px;
  margin-left: auto;
  margin-right: auto;
  list-style-type: none;
  font-size: 22px;
  font-weight: 400;
  line-height: 48px;
  text-align: left;

  @include on-mob-tabl {
    font-size: 14px;
    line-height: 40px;
  }

  &:hover {
    font-weight: 700;
  }
}

.top-bar {
  &__group--m {
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 20px;
    margin-top: 26px;
  }
}

.switch-lang--m {
  display: flex;
  flex-direction: row;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;

  @include on-tablet {
    display: none;
  }

  &--e {
    cursor: pointer;
    color: #131313;

    @include hover(transform, scale(1.2));
  }

  &--u {
    cursor: pointer;
    color: #bdbdbd;

    @include hover(transform, scale(1.2));
  }
}

.specs--m {
  text-decoration: none;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #131313;
  cursor: pointer;

  @include hover(transform, scale(1.2));

  @include on-tablet {
    display: none;
  }
}

.button__buy {
  display: flex;
  justify-content: center;
  align-items: end;

  width: 100%;
  height: calc(100% - 249px);

  &--m {
    width: inherit;
    height: 48px;
    background: #333;
    font-size: 12px;
    font-weight: 700;
    line-height: 14px;
    color: white;

    @include on-tablet {
      display: none;
    }

    &:hover {
      cursor: pointer;
      background-color: #131313;
    }
  }
}
