.see-more {
  text-align: left;
  text-transform: uppercase;
  color: #0db2b3;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  position: relative;
}

.see-more:before {
  content: "";
  border-bottom: 1px solid #0db2b3;
  width: 64px;
  display: block;
  position: absolute;
  bottom: -5px;
}

.see-more:after {
  content: "";
  transform-origin: 0;
  border-bottom: 1px solid #0db2b3;
  width: 128px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -5px;
  transform: scale(0);
}

.see-more:hover:after {
  transform: scale(1);
}

